export default function BuyingPage() {
  const [selectedTariff, setSelectedTariff] = useState<string | null>(null)
  const [billingPeriod, setBillingPeriod] = useState<BillingPeriod>("month")
  const [serverName, setServerName] = useState("")
  const [loading, setLoading] = useState(false)
  const [success, setSuccess] = useState(false)
  const { t } = useLanguage()
  const { formatPrice, convertPrice } = useCurrency()

  const handleBuy = async () => {
    if (!selectedTariff || !serverName) return

    setLoading(true)
    setSuccess(false)
    const tariff = tariffs.find(t => t.id === selectedTariff)
    if (!tariff) return

    try {
      // Здесь должен быть реальный процесс заказа сервера через ваш API
      // Пример запроса:
      await fetch("/api/orders", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          serverName,
          serverPlan: tariff.name,
          billingPeriod,
          price: convertPrice(tariff.prices[billingPeriod]), // Используем convertPrice, если нужно конвертировать
        }),
      })

      setSuccess(true)
      setTimeout(() => setSuccess(false), 4000)
    } finally {
      setLoading(false)
    }
  }

  return (
    <div className="min-h-screen bg-background">
      <Header />
      <div className="max-w-6xl mx-auto py-8 px-4 sm:py-12 sm:px-6 lg:px-8">
        <Button variant="outline" className="mb-4 sm:mb-6" type="button" onClick={() => window.history.back()}>
          ← {t("common.back") || "Back"}
        </Button>
        <h1 className="text-2xl sm:text-3xl font-bold text-foreground mb-6 sm:mb-8">{t("servers.buyTitle") || "Buy Server"}</h1>
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6 sm:gap-8">
          {/* Tariff List */}
          <div className="order-2 md:order-1">
            <h2 className="text-xl sm:text-2xl font-semibold mb-3 sm:mb-4">{t("servers.choosePlan") || "Choose Plan"}</h2>
            <div className="space-y-3 sm:space-y-4 max-h-[calc(100vh-200px)] sm:max-h-[calc(100vh-300px)] overflow-y-auto pr-2 sm:pr-4 scrollbar-thin scrollbar-thumb-rounded scrollbar-thumb-primary/20">
              {tariffs.map((tariff) => (
                <div
                  key={tariff.id}
                  className={`transition-all cursor-pointer border rounded-lg sm:rounded-xl p-4 sm:p-5 bg-card flex flex-col sm:flex-row items-start sm:items-center gap-3 sm:gap-4 shadow-sm hover:shadow-md ${
                    selectedTariff === tariff.id ? "border-primary shadow-md" : "border-border hover:border-primary/50"
                  }`}
                  onClick={() => setSelectedTariff(tariff.id)}
                >
                  <div className="w-10 h-10 sm:w-12 sm:h-12 rounded-md sm:rounded-lg flex items-center justify-center bg-primary/5 flex-shrink-0">
                    <Server className="w-5 h-5 sm:w-6 sm:h-6 text-primary" />
                  </div>
                  <div className="flex-1 w-full">
                    <div className="font-semibold text-base sm:text-lg text-foreground">{tariff.name}</div>
                    <div className="text-xs sm:text-sm text-muted-foreground mt-1">
                      {tariff.cores} CPU &middot; {tariff.ram} GB {tariff.ramType} &middot; {tariff.storage} GB {tariff.storageType}
                    </div>
                  </div>
                  <div className="flex flex-row sm:flex-col items-end gap-2 sm:gap-1 min-w-[120px] sm:min-w-[140px] w-full sm:w-auto justify-between sm:justify-start">
                    <div className="text-base sm:text-xl font-bold text-primary">
                      {formatPrice(tariff.prices.month)} <span className="text-xs sm:text-sm text-muted-foreground">/{t("servers.perMonth") || "mo"}</span>
                    </div>
                    <div className="text-sm sm:text-md font-semibold text-foreground">
                      {formatPrice(tariff.prices.hour)} <span className="text-xs sm:text-sm text-muted-foreground">/{t("servers.perHour") || "hr"}</span>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </div>
          {/* Order Panel */}
          <div className="order-1 md:order-2 space-y-4 sm:space-y-6 md:sticky md:top-20 self-start">
            <h2 className="text-xl sm:text-2xl font-semibold">{t("servers.orderConfig") || "Order Configuration"}</h2>
            <div className="flex flex-col gap-4 sm:gap-5 bg-card p-4 sm:p-6 rounded-lg sm:rounded-xl shadow-sm border border-border">
              <div>
                <label className="block mb-1 sm:mb-2 font-medium text-foreground">{t("servers.nameLabel") || "Server Name"}</label>
                <Input
                  placeholder={t("servers.namePlaceholder") || "e.g., my-server"}
                  value={serverName}
                  onChange={e => setServerName(e.target.value)}
                  disabled={loading}
                  className="w-full text-sm sm:text-base"
                />
              </div>
              <div>
                <label className="block mb-1 sm:mb-2 font-medium text-foreground">{t("servers.billing") || "Billing Period"}</label>
                <div className="flex gap-2 sm:gap-3">
                  <Button
                    variant={billingPeriod === "month" ? "default" : "outline"}
                    onClick={() => setBillingPeriod("month")}
                    className="flex-1 text-sm sm:text-base py-2 sm:py-0"
                    disabled={loading}
                  >
                    {t("servers.monthly") || "Monthly"}
                  </Button>
                  <Button
                    variant={billingPeriod === "hour" ? "default" : "outline"}
                    onClick={() => setBillingPeriod("hour")}
                    className="flex-1 text-sm sm:text-base py-2 sm:py-0"
                    disabled={loading}
                  >
                    {t("servers.hourly") || "Hourly"}
                  </Button>
                </div>
              </div>
              {selectedTariff && (
                <div>
                  <p className="font-medium mb-1 sm:mb-2 text-foreground">{t("servers.selectedPlan") || "Selected Plan:"}</p>
                  <div className="flex flex-col sm:flex-row items-start sm:items-center justify-between bg-background p-3 rounded-lg border border-border gap-2 sm:gap-0">
                    <Badge variant="secondary" className="text-xs sm:text-sm">
                      {tariffs.find(t => t.id === selectedTariff)?.name}
                    </Badge>
                    <span className="font-semibold text-primary text-sm sm:text-base">
                      {formatPrice(tariffs.find(t => t.id === selectedTariff)?.prices[billingPeriod] || 0)}
                      <span className="text-muted-foreground ml-1">
                        /{billingPeriod === "month" ? (t("servers.perMonth") || "mo") : (t("servers.perHour") || "hr")}
                      </span>
                    </span>
                  </div>
                </div>
              )}
              <Button
                className="w-full mt-2 sm:mt-4 text-sm sm:text-base"
                onClick={handleBuy}
                disabled={!selectedTariff || !serverName || loading}
                size="lg"
              >
                {loading
                  ? t("servers.buying") || "Purchasing..."
                  : t("servers.buyButton") || "Purchase"}
              </Button>
              {success && (
                <div className="text-green-600 font-medium text-center mt-2 sm:mt-3 text-sm sm:text-base">
                  {t("servers.successOrder") || "Server successfully ordered!"}
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}